@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
.card--section{display:flex;justify-content:space-around;align-items:center;flex-wrap:wrap}.card--section .card--wrapper{width:30%;margin:0 auto;display:flex;flex-direction:column;align-items:left;background-color:#fe0;margin-top:15px;box-shadow:4px 4px #ff0080, 4px -4px #00ffc8;padding:5px;min-height:100%;margin-bottom:20px}.card--section .card--wrapper .card__size{display:flex;justify-content:space-between;margin-bottom:10px}.card--section .card--wrapper .card__size .text__size{color:#120b2c;text-shadow:2px 2px #00ffc8;font-size:16px;font-weight:700}.card--section .card--wrapper .card__size .select__size{width:40%;background-color:transparent;border:2px solid #120b2c;border-radius:5px;height:30px;box-shadow:2px 2px #00ffc8}.card--section .card--wrapper .card__color{display:flex;justify-content:space-between;margin-bottom:10px}.card--section .card--wrapper .card__color .text__color{color:#120b2c;text-shadow:2px 2px #00ffc8;font-size:16px;font-weight:700}.card--section .card--wrapper .card__color .select__color{width:40%;background-color:transparent;border:2px solid #120b2c;border-radius:5px;height:30px;box-shadow:2px 2px #00ffc8}.card--section .card--wrapper .card__quantity{display:flex;justify-content:space-between;margin-bottom:20px}.card--section .card--wrapper .card__quantity .text__quantity{color:#120b2c;text-shadow:2px 2px #00ffc8;font-size:16px;font-weight:700}.card--section .card--wrapper .card__quantity .select__quantity{width:40%;background-color:transparent;border:2px solid #120b2c;border-radius:5px;height:30px;box-shadow:2px 2px #00ffc8}.card--section .card--wrapper .card__button{height:60px;background-color:#120b2c;color:#fe0;border:none;box-shadow:2px 2px #00ffc8;margin-bottom:10px;font-weight:700;transition:all .3s ease}.card--section .card--wrapper .card__button:hover{color:#ff0080;cursor:pointer}.card--section .card--wrapper .card_name{text-align:left;color:#120b2c;text-shadow:0.06em 0.06em #ff0080;font-family:"Staatliches", cursive;font-size:35px;letter-spacing:0.1em;margin-top:5px;margin-bottom:5px}.card--section .card--wrapper .card_model{margin-top:10px;margin-bottom:5px;text-align:left;color:#120b2c;font-family:"Staatliches", cursive;font-size:20px;letter-spacing:0.1em}.card--section .card--wrapper .card__shipping{margin-top:10px;margin-bottom:5px;text-align:left;color:#120b2c;font-family:"Staatliches", cursive;font-size:20px;letter-spacing:0.1em;margin-bottom:20px}.card--section .card--wrapper .card__desc{text-align:left;color:#120b2c;font-size:16px;margin-bottom:5px;font-weight:500}.card--section .card--wrapper .card__price{font-size:30px;font-weight:700;color:#ff0080;margin-top:20px}.card--section .card--wrapper .img__wrapper{overflow:hidden;display:inline-block;box-shadow:2px 2px #ff0080, 2px -2px #00ffc8}.card--section .card--wrapper .img__wrapper .card_img{width:100%;transition:1s;display:block}.card--section .card--wrapper .img__wrapper .card_img:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}body{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:linear-gradient(90deg, #1a042c, #231b3f, #2e197c, #2e197c, #2f0642);width:100%;height:100vh}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

